* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
body {
  background-color: #000;
}
.nav {
  height: 100%;
  width: 100%;
  background-color: rgba($color: #000, $alpha: 0.5);
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 5vw;
}
.logo {
  height: 9dvh;
}
main {
  height: 100dvh;
  position: relative;
  background-image: url(images/background.webp);
  background-size: 150vw;
  background-position: 45%;
  display: grid;
  place-items: center;
  grid-template-rows: 12dvh 76dvh 12dvh;
}
.title {
  font-family: "Montserrat", sans-serif;
  color: white;
  font-size: 4rem;
  // transform: translate(0, -10dvh);
}
.insta-logo {
  font-size: 1.4rem;
}

@media screen and (max-width: 600) {
  .nav {
    height: 100%;
    width: 100%;
    background-color: rgba($color: #000, $alpha: 0.5);
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 2rem;
  }
  .logo {
    height: 6dvh;
  }
  main {
    height: 100dvh;
    width: 100vw;
    position: relative;
    background-image: url(images/background.webp);
    background-size: 400%;
    background-position: 45%;
    display: grid;
    place-items: center;
    grid-template-rows: 8dvh 84dvh 8dvh;
  }
  .title {
    font-family: "Montserrat", sans-serif;
    color: white;
    font-size: 4rem;
    text-align: center;
    // transform: translate(0, -10dvh);
  }
  .insta-logo {
    font-size: 1.4rem;
  }
}
